	module.exports = {"data":{"wordpressWpLessons":{"id":"f418c6c8-7a35-5241-b62c-281895c5a7ca","wordpress_id":1106,"title":"Gimp: Pixel Art Character Illustration","slug":"gimp-pixel-art-character-illustration","content":"<h2>Objectives and Overview</h2>\n<p>Pixel art was done out of necessity in the early console games, but has recently surged in popularity in the indie game community, partly due to the success of <em>Minecraft</em>. Pixel art is lots of fun to create, and you&#8217;ll be developing pixel art characters that you&#8217;ll use when you start the <strong>Game Development With Scratch</strong> module.</p>\n<p>This is a fun way to practice navigating Gimp and developing a graphic design workflow. The skills that you&#8217;ll use in this project translate directly to Adobe Photoshop, one of the pillars of professional graphic design. Pixel art pairs very well with 2d games, especially platformers. This lesson will walk you through the basics, but the main goal is to get you exploring and creating so that you&#8217;ll refine your Gimp skills along the way!</p>\n<h3>Lesson Objectives:</h3>\n<ul>\n<li>Practice navigating in Gimp</li>\n<li>Understand how to create pixel art with Gimp</li>\n<li>Become familiar with pixel art techniques, such as shading and dithering</li>\n<li>Develop an eye for working with color and color palettes</li>\n<li>Create pixel art character and other assets for your game</li>\n</ul>\n<hr />\n<h2>Creating Pixel Art</h2>\n<p>One of the key distinctions of <strong>pixel art</strong> from <strong>vector art</strong> is that pixel art is created with <strong>raster graphic software</strong>. Pixel art can be created with any software that allows for the creation of squares in a zoomed in the grid, but Gimp has a faster workflow because it allows for setting the pencil to a pixel, enabling the user to paint with pixels.</p>\n<p>Many professional pixel artists use Photoshop to create their art, and the skills that you practice while creating art with Gimp will transfer to Photoshop if you decide to pursue that in the future.</p>\n<h2>Getting Started With Gimp</h2>\n<h3>Launching Gimp</h3>\n<p>Click on the magnifying glass at the top right of your desktop and search for “Gimp.” This will locate the file. Go ahead and click on it to launch Gimp! Alternatively, you can use the Launchpad to start the software. Launchpad is the rocket ship icon on the Dock that you used to launch Inkscape.</p>\n<p>You should have Gimp pre-installed, but in case you don’t here is the download link: <a href=\"https://www.gimp.org/downloads/\">Gimp Download</a>. Navigate to that page and click <strong>Download Gimp 2.8.16 directly</strong>. The download is a .dmg file which you can open once it finishes.</p>\n<p>As a supplement to the in-class demo, here is a video that will walk you through how to set up your Gimp workplane and save your creation.</p>\n<p><iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/ZlyuNQ77CYs\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<h2><span style=\"color: #0091c9;\">Activity: Create a Sprite</span></h2>\n<p>Now that you have an understanding of pixel art it&#8217;s time to get creating! Pixel art is fun and there is quite a lot of room for creativity and complexity. This activity is a chance for you to get some practice with Gimp before you start creating sprites for a Scratch game. Create a graphic prop (i.e. a coin or tree).</p>\n<h3>Helpful Presets for Pixel Art</h3>\n<p>One great feature in Gimp is the ability to set presets for your tools. Here are some helpful presets for you to reference:</p>\n<ul>\n<li>New Project Image Settings:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/Interactive_Gaming_-_Pixel_Art_-_Gimp_New_Image-600px1.jpg\" alt=\"Gimp New Project Image Settings Pixel Art\" /></li>\n<li>Zoomed in Workplane:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/Interactive_Gaming_-_Pixel_Art_-_Zoomed_In_Workplane-600px4.jpg\" alt=\"Gimp Zoomed in Workplane Pixel Art\" /></li>\n<li>Pencil Preset:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/Interactive_Gaming_-_Pixel_Art_-_Tool_Options__Pencil_-600px3.jpg\" alt=\"Gimp Pencil Preset Pixel Art\" /></li>\n<li>Eraser Preset:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/Interactive_Gaming_-_Pixel_Art_-_Tool_Options__Eraser_-600px2.jpg\" alt=\"Gimp Eraser Preset Pixel Art\" /></li>\n</ul>\n<h2>Sizing for Pixel Art</h2>\n<p>Generally, you&#8217;ll want your pixel art to be done in powers of 2, such as <strong>16x16px</strong>, <strong>32x32px</strong>, and <strong>64x64px</strong>. For the backdrops, it&#8217;s recommended that you use the size of the Scratch screen, <strong>480x360px</strong>. This way you&#8217;ll know that what you&#8217;re making will correctly correspond to the Scratch screen size.</p>\n<h2>Color and Shading Techniques for Pixel Art</h2>\n<p>One of the most interesting and creative aspects of pixel art is the use of limited colors to create shades and textures in the pieces. There are some different techniques on it, and you should do some google or YouTube searches if you&#8217;re looking for more instruction. However, here are some basics to get you started:</p>\n<p><strong>Dithering</strong> is a technique employed by several pixel artists in shading and blending colors. Here is a great comparison of a non-dithered and a dithered sphere, courtesy of<br />\na Pixel Art in Photoshop blog post by Indium Games:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/IGD%20-%20Pixel%20Art%20-%20Dither%20Example%20indium%20blog-600px1.jpg\" alt=\"Dithering Example Indium Games\" /></p>\n<p>The same principles used in Photoshop apply to Gimp, so if you&#8217;re finding examples for Photoshop you can almost always apply those principles to Gimp as well.</p>\n<p>Link for more detail: <a href=\"http://blog.indiumgames.fi/2014/07/04/pixel-art-photoshop/\">Dithering Blog Post from Indium Games.</a></p>\n<p>Dithering is used more often in larger pieces such as backdrops as opposed to smaller pieces like characters and objects. Dithering helps create transitions between colors and enables complex shading and blending to be done with a rather limited color palette. If you&#8217;re going to be using a limited palette such as the original NES color set that is linked below in the resources, you&#8217;ll likely want to experiment with dithering. It takes a lot of practice before honing your style. There&#8217;s several resources out there specifically about dithering, so if you&#8217;re interested in developing this skill do some searches on YouTube or Google!</p>\n<p><strong>Traditional shading</strong> techniques are also employed by artists. Shading in pixel art provides a lot of the charm associated with it and it is highly recognizable. Consider this pixel heart created by tomred12 at <a href=\"http://http-//piq.codeus.net/\">piq.codeus.net</a> as an example:</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/IGD%20-%20Pixel%20Art%20-%20Pixel%20Heart%20Example%20tomred12-600px1.jpg\" alt=\"Pixel Heart Example - tomred12 - piq.codeus.net\" /></p>\n<p>While the shading isn&#8217;t necessary, it contributes to the immediately recognizable style associated with pixel art.</p>\n<h2>Further Skill Development</h2>\n<p>Here is a great resource with examples. It is highly recommended you look through this guide:<br />\n<a href=\"http://orig08.deviantart.net/5ae2/f/2007/023/3/0/pixel_shading_tutorial_by_kitted.png\">Pixel Shading Tutorial by Kitted</a> It is a large image. I would recommend opening it in another tab and zooming in for the instructions and examples.</p>\n<p>Here are two more links about light and shading. They aren&#8217;t specifically for pixel art but are excellent resources if you&#8217;re interested in understanding more about the relationship between light sources and shading. These concepts can be applied to your pixel art.</p>\n<p>Additional Shading Resources:</p>\n<ul>\n<li><a href=\"http://calisto-lynn.deviantart.com/art/light-and-shadow-guide-68593673\">Calisto Lynn&#8217;s Light and Shading Guide</a></li>\n<li><a href=\"http://www.artinstructionblog.com/drawing-lesson-a-theory-of-light-and-shade#more-1645\">A Theory of Light and Shade</a></li>\n</ul>\n<p><strong>Color choice</strong> is another important aspect of pixel art creation. Early video games for the Atari, Commodore 64, and Nintendo had limited color palettes available for their sprites and backgrounds. As a result, pixel artists had to be highly creative with their color usage. If you&#8217;re interested in obtaining a truly retro feel to your game while simultaneously working within the same color constraints that the early designers did, you should restrict your color choice to the original palette available for the NES!</p>\n<p>If not, consider using things like black borders around your characters to help them stand out against the backdrops. Additionally, consider using black to draw attention to certain key characteristics of your sprites, such as the eyes, mouth, or character outline.</p>\n<p>You can also use contrast to highlight key aspects of your characters. For example, if you have a character with mostly dark coloring, adding even a single light colored pixel can drastically alter the appearance, such as in the heart example above.</p>\n<h4>Things to Consider:</h4>\n<p>Pixel art is a fun way to develop having an eye for color, which is a key element of graphic design. There are some key ways you can practice this skill, such as working with a set color palette for your character art.<br />\nAnother way that making pixel art fits into graphic design is that you&#8217;ll begin to develop an eye for how small color changes can have a large impact on your project. Paying attention to these subtle changes will further develop your eye for color and design.</p>\n<p>Here are some great resource for working with color/color palettes:</p>\n<ul>\n<li style=\"list-style-type: none;\">\n<ul>\n<li><a href=\"https://color.adobe.com/create/color-wheel/\">Adobe Kuler</a></li>\n<li><a href=\"http://www.color-hex.com/\">Color Hex</a>: Contains colors and their hex values.</li>\n<li><a href=\"http://www.colourlovers.com/\">Colour Lovers</a></li>\n</ul>\n</li>\n</ul>\n<hr />\n<h2>Additional Resources</h2>\n<ul>\n<li><a href=\"http://www.thealmightyguru.com/Games/Hacking/Wiki/index.php?title=NES_Palette\">Original NES Color Palette</a></li>\n<li><a href=\"http://www.gamasutra.com/blogs/DavidDAngelo/20140625/219383/Breaking_the_NES_for_Shovel_Knight.php\">Shovel Knight: Breaking the NES</a></li>\n<li><a href=\"https://www.youtube.com/watch?v=SoOvMj_fRQo\">How to Make Minecraft Pixel Art with Gimp</a></li>\n</ul>\n"}},"pathContext":{}}

